.za-swipe {
  position: relative;
  overflow: hidden; }
  .za-swipe-item {
    display: inline-block;
    width: 100%;
    height: 100%;
    font-size: 14px; }
  .za-swipe-pagination {
    position: absolute;
    right: 10px;
    bottom: 10px; }
    .za-swipe-pagination ul {
      list-style: none;
      margin: 0;
      padding: 0;
      font-size: 0; }
      .za-swipe-pagination ul li {
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background: #ccc;
        cursor: pointer;
        margin: 3px 0 0 3px; }
        .za-swipe-pagination ul li.active {
          background: #12c287; }
